import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import locationActions from "../../../../redux/companyMaster/location/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class Location extends Component {
  state = {
    filter: {
      id_site: [],
      id_location_type: [],
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getLocations } = this.props;
    getLocations();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.location.error) {
      notification("error", this.props.location.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.location.success) {
      notification("success", this.props.location.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    // console.log(value);
    const { getLocations } = this.props;
    this.setState({
      filter: {
        id_site: [],
        id_location_type: [],
        pagination: null
      }
    });
    getLocations(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getLocations } = this.props;
    this.setState({
      filter: {
        id_site: [],
        id_location_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getLocations();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getLocations } = this.props;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location_type) {
      string += "id_location_type=" + filter.id_location_type;
    }
    if (filter.id_site) {
      string
        ? (string += "&id_site=" + filter.id_site)
        : (string += "id_site=" + filter.id_site);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    this.setState({ search: null });
    getLocations(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getLocations } = this.props;
    this.setState({
      filter: {
        id_site: [],
        id_location_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getLocations();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateLocation(id);
    } else {
      this.props.deactivateLocation(id);
    }
  };

  // Handle pagiantion
  handlePageChange = page => {
    const { getLocations } = this.props;
    const { search, filter } = this.props.location;
    getLocations(page, search, filter);
  };

  actionDetailOnly = {
    title: "",
    dataIndex: "id_location",
    rowKey: "id_location",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip placement="top" title={<IntlMessages id="tooltip.detail" />}>
          <Link to={`${this.props.match.path}/show/${text}`}>
            <Button size="small" className="invoiceMrBtn">
              <i className="ion-more" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  listAction = {
    title: "",
    dataIndex: "id_location",
    rowKey: "id_location",
    // width: 100,
    fixed: "right",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {/* <Tooltip placement="top" title={<IntlMessages id="tooltip.detail" />}>
          <Link to={`${this.props.match.path}/show/${text}`}>
            <Button size="small" className="invoiceMrBtn">
              <i className="ion-more" />
            </Button>
          </Link>
        </Tooltip> */}

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="locations.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="locations.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "location_code",
        rowKey: "location_code",
        width: "10%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.site" }),
        dataIndex: "site.site_name",
        rowKey: "site.site_name",
        width: "30%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.locationType" }),
        dataIndex: "location_type.location_type_name",
        rowKey: "site.site_name",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "inventoryType" }),
        dataIndex: "inventory_type",
        rowKey: "inventory_type",
        width: "15%",
        render: text => {
          if (text === 1) {
            return <span>Normal</span>;
          } else {
            return <span>Minus</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // console.log(this.state.filter);

    // Data setup
    const { locations, loading } = this.props.location;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    if (permissions.includes("Update Location") && columns.length === 6) {
      columns.push(this.listAction);
    }
    // else if (columns.length === 6) {
    //   columns.push(this.actionDetailOnly);
    // }

    Object.keys(locations).map((location, index) => {
      return dataSource.push({
        ...locations[location],
        key: location
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.location;

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="locations.pageHeader" />}
            marginBottom={true}
          >
            <div>
              {permissions.includes("Create Location") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button
                    // loading={loading}
                    // disabled={loading}
                    type="primary"
                    className="mateAddInvoiceBtn"
                  >
                    <IntlMessages id="locations.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="locationTypes.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location_type"
                      )}
                      value={
                        this.state.filter.id_location_type
                          ? this.state.filter.id_location_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="locations.filter.select.locationType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {this.props.location.location_types.map((data, key) => (
                        <Option key={key} value={data.id_location_type}>
                          {data.location_type_name}
                        </Option>
                      ))}
                    </Select>
                  </div>
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="sites.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "id_site")}
                      value={
                        this.state.filter.id_site
                          ? this.state.filter.id_site
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="locations.filter.select.site" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {this.props.location.sites.map((data, key) => (
                        <Option key={key} value={data.id_site}>
                          {data.site_name}
                        </Option>
                      ))}
                    </Select>
                  </div>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "10px" }}
                >
                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "90%" }}
                      placeholder={
                        <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.location.loading}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "33%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {locations.length === 0 && !this.props.location.loading ? (
              <Empty
                description={<IntlMessages id="locations.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.location.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    location: state.location,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, locationActions))(Location);
